import HorizontalTabWrapper from "../../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../../Ui/Tab/Horizontal/HorizontalTabCard";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import style from "../../../FinanceManager/FinanceManager.module.css";
import PayrollReturnMonthly from "./PayrollReturnMonthly";
import PayrollReturnAnnually from "./PayrollReturnAnnually";
import PayrollReturnQuartly from "./PayrollReturnQuartly";
import { assestsUtilityUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
const PayrollReturnHead = ({ pid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();

  const onBack = () => {
    navigate(`/q/${params.username}/member/staff/payroll?type=return`, {
      state: {
        ...getQuery.state,
        heads_key: "",
      },
    });
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div className={style.fm_header_start_container}>
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onBack}
              />
              <h6>
                {getQuery.state?.heads_key ?? ""}{" "}
                {getQuery.state?.headCard?.name ?? ""},{" "}
                {getQuery.state?.financialYear ?? ""}
              </h6>
            </div>
          </div>
        </UserMemberContentWrapper>
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("d"))}
              parentState={getQuery.state}
              url={`/q/${params.username}/member/staff/payroll?type=return&n=head${tab.urlKey}`}
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginBlock: "0",
          }}
        />
        <UserMemberContentWrapper>
          {searchParams.get("d") === "quartly" ? (
            <PayrollReturnQuartly pid={pid} />
          ) : searchParams.get("d") === "annually" ? (
            <PayrollReturnAnnually pid={pid} />
          ) : (
            <PayrollReturnMonthly pid={pid} />
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default PayrollReturnHead;

const tabList = [
  {
    label: "monthly",
    id: "monthly",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "quartly",
    id: "quartly",
    urlKey: "&d=quartly",
    groupIs: ["quartly"],
  },
  {
    label: "annually",
    id: "annually",
    urlKey: "&d=annually",
    groupIs: ["annually"],
  },
];
