import { useTranslation } from "react-i18next";
import style from "../../../FinanceManager/Income/Income.module.css";
import { useState } from "react";
import { usePayrollAddReturnDetail } from "../../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import { useFileUpload } from "../../../../../hooks/authentication-api";
import FormFile from "../../../../Ui/Input/FormFile";
import FormInput from "../../../../Ui/Input/FormInput";
const AddQuartlyReturn = ({
  onClose,
  onRefetch,
  pid,
  headKey,
  fYear,
  fMonth,
}) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    return_attach: "",
    challan_attach: "",
    receipt_no: "",
    price: "",
  });
  const [payrollAddReturnDetail] = usePayrollAddReturnDetail();
  const [fileUpload] = useFileUpload();

  const onFileUpload = (e, flow) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            [flow]: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onReturnMonthly = () => {
    let validation = requiredField(state, []);
    if (
      existingRequiredField(validation) &&
      pid &&
      headKey &&
      fYear &&
      fMonth
    ) {
      setDisabled((pre) => !pre);
      payrollAddReturnDetail({
        pid: pid,
        bodyParameter: {
          ...state,
          price: +state.price,
          month: fMonth,
          year: fYear,
          key: headKey,
          quarter: returnQuarter(fMonth),
          flow: "QUARTERLY",
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  // console.info("pid", state);
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("quartly_return")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("price")}
              placeholder={t("price_placeholder")}
              name={"price"}
              value={state.price}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["price"]}
            />
            <FormInput
              label={t("receipt_no")}
              placeholder={t("receipt_no_placeholder")}
              name={"receipt_no"}
              value={state.receipt_no}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["receipt_no"]}
            />
            <FormFile
              label={t("rertun_attachment")}
              name={"rertun_attachment"}
              value={
                state.return_attach
                  ? {
                      key: state.return_attach,
                      name: "View",
                    }
                  : ""
              }
              onChange={(e) => onFileUpload(e, "return_attach")}
              errorShow={errorField["rertun_attachment"]}
            />
            <FormFile
              label={t("challan_attachment")}
              name={"challan_attachment"}
              value={
                state.challan_attach
                  ? {
                      key: state.challan_attach,
                      name: "View",
                    }
                  : ""
              }
              onChange={(e) => onFileUpload(e, "challan_attach")}
              errorShow={errorField["challan_attach"]}
            />

            <CreateButton label="save" onAction={onReturnMonthly} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default AddQuartlyReturn;

const returnQuarter = (val) => {
  if (["04", "05", "06", "07"]?.includes(val)) {
    return "Q1";
  }
  if (["08", "09", "10", "11"]?.includes(val)) {
    return "Q2";
  }
  if (["12", "01", "02", "03"]?.includes(val)) {
    return "Q3";
  }
};
