import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import style from "../../FinanceManager/FinanceManager.module.css";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useCalendar } from "../../../Hooks/CustomHooks/use-calendar";
import dayjs from "dayjs";
import { useState } from "react";
import SelectFinancialYear from "../SelectFinancialYear";
import PayrollReturnCard from "./PayrollReturnCard";
import PayrollReturnHead from "./PayrollReturnHead/PayrollReturnHead";
import PayrollReturnMonthCard from "./PayrollReturnMonthCard";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";

const PayrollReturn = ({ instituteId, pid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const { selectedDate, setSelectedDate } = useCalendar();
  const [financialYear, setFinancialYear] = useState(false);

  const params = useParams();
  const navigate = useNavigate();

  const onBack = () => {
    navigate(`/q/${params.username}/member/staff/payroll`, {
      state: {
        ...getQuery.state,
        heads_key: "",
        headCard: "",
      },
    });
  };

  const onYearToggle = () => {
    setFinancialYear((pre) => !pre);
  };
  const onYearChoose = (value) => {
    setSelectedDate(
      (prev) => new Date(`${prev.getMonth() + 1} ${prev.getDate()} ${value}`)
    );
    onYearToggle();
  };
  return (
    <>
      {searchParams.get("n") === "head" ? (
        <PayrollReturnHead pid={pid} />
      ) : searchParams.get("n") === "year" ? (
        <UserMemberContainerWrapper>
          <UserMemberContentWrapper>
            <div className={style.fm_header_container}>
              <h6>{t("return")}</h6>
              <div className={style.fm_header_start_container}>
                <h6
                  style={{
                    cursor: "pointer",
                  }}
                  onClick={onYearToggle}
                >
                  {t("financial_year")} : {dayjs(selectedDate).format("YYYY")}
                </h6>
              </div>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper>
            {monthList?.map((mt) => (
              <PayrollReturnMonthCard
                key={mt?.name}
                mt={mt}
                financialYear={dayjs(selectedDate).format("YYYY")}
              />
            ))}
          </UserMemberContentWrapper>
        </UserMemberContainerWrapper>
      ) : (
        <UserMemberContainerWrapper>
          <UserMemberContentWrapper>
            <div className={style.fm_header_container}>
              <div className={style.fm_header_start_container}>
                <img
                  alt="search icon"
                  src={`${assestsUtilityUrl}/back.svg`}
                  style={{
                    height: "2rem",
                    width: "2rem",
                  }}
                  onClick={onBack}
                />
                <h6>
                  {t("return")} {t("of")} {getQuery.state?.headCard?.name ?? ""}
                  , {getQuery.state?.financialYear ?? ""}
                </h6>
              </div>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper>
            {returnType?.map((rt) => (
              <PayrollReturnCard rt={rt} key={rt} />
            ))}
          </UserMemberContentWrapper>
        </UserMemberContainerWrapper>
      )}

      {financialYear && (
        <SelectFinancialYear
          onClose={onYearToggle}
          onYearChoose={onYearChoose}
        />
      )}
    </>
  );
};

export default PayrollReturn;

const returnType = ["EPF", "GRAUITY", "ESI", "PT", "TDS"];

const monthList = [
  {
    name: "April",
    value: "04",
  },
  {
    name: "May",
    value: "05",
  },
  {
    name: "June",
    value: "06",
  },
  {
    name: "July",
    value: "07",
  },
  {
    name: "August",
    value: "08",
  },
  {
    name: "September",
    value: "09",
  },
  {
    name: "October",
    value: "10",
  },
  {
    name: "November",
    value: "11",
  },
  {
    name: "December",
    value: "12",
  },
  {
    name: "January",
    value: "01",
  },
  {
    name: "February",
    value: "02",
  },
  {
    name: "March",
    value: "03",
  },
];
