import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";
import { usePayrollOneReturnHeadDetail } from "../../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
import style from "../../../FinanceManager/FinanceManager.module.css";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useState } from "react";
import PayrollReturnMonthlyCard from "./PayrollReturnMonthlyCard";
import Loading from "../../../../Loader/Loading";
import AddMonthlyReturn from "./AddMonthlyReturn";
const PayrollReturnMonthly = ({ pid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [add, setAdd] = useState(false);

  const {
    payrollOneReturnHeadDetail,
    payrollOneReturnHeadDetailLoading,
    payrollOneReturnHeadDetailRefetch,
  } = usePayrollOneReturnHeadDetail({
    data: {
      pid: pid,
      month: getQuery.state?.headCard?.value,
      year: getQuery.state?.financialYear,
      key: getQuery.state?.heads_key,
      quarter: "",
      flow: "MONTHLY",
    },
    skip: !pid,
  });

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  //   console.info("sfsd", getQuery.state);
  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBottom: "0.5rem",
        }}
      >
        <div></div>
        <CreateButton
          label="add"
          customStyle={{
            paddingBlock: "0.4rem",
            margin: "0",
          }}
          onAction={onToggle}
        />
      </div>
      {payrollOneReturnHeadDetail && (
        <PayrollReturnMonthlyCard numReturn={payrollOneReturnHeadDetail} />
      )}
      {payrollOneReturnHeadDetailLoading && <Loading />}

      {add && (
        <AddMonthlyReturn
          pid={pid}
          onClose={onToggle}
          onRefetch={payrollOneReturnHeadDetailRefetch}
          headKey={getQuery.state?.heads_key}
          fYear={getQuery.state?.financialYear}
          fMonth={getQuery.state?.headCard?.value}
        />
      )}
    </>
  );
};

export default PayrollReturnMonthly;
